import React from 'react';
import {Link} from "react-router-dom";
import imgUser from '../../img/user.png'
import PropTypes from "prop-types";

const Profile = ({user}) => {
    const {name, photoUrl, phone, address, email} = user;
    return (
        <section className='account'>
            <h2 className="account-name">{name}</h2>
            <div className="user">
                <img src={photoUrl || imgUser} alt="" className='user-img'/>
                <article className='user-info'>
                    <p><i className='fas fa-at'/>{phone}</p>
                    <p><i className='fas fa-phone'/> {address}</p>
                    <p><i className='fas fa-address-book'/> {email}</p>
                </article>

            </div>
            <Link to='/logout'>
                <button className='logout'>
                    <i className='fa fa-power-off'/> Logout
                </button>
            </Link>
        </section>
    )
};

Profile.propTypes = {
    user: PropTypes.shape({
        name: PropTypes.string.isRequired,
        phone: PropTypes.number.isRequired,
        address: PropTypes.string.isRequired,
        email: PropTypes.string.isRequired,
        photoUrl: PropTypes.string
    })
};

export default Profile;