import React from 'react';
import {NavLink} from "react-router-dom";
import logo from '../../img/Capture.JPG'

const Menu = () => (
    <header className='header'>
        <div className='company-logo'>
            <img src={logo} alt=""/>
        </div>
        <nav className='menu'>
            <NavLink exact to='/' className='menu-items' activeClassName='active'>
                <i className='fas fa-home'/>
                <span className='menu-items-text'>Home</span>
            </NavLink>
            <NavLink to='/messages' className='menu-items' activeClassName='active'>
                <i className='fas fa-envelope'/>
                <span className='menu-items-text'>Messages</span>
            </NavLink>
            <NavLink to='/wishlist' className='menu-items' activeClassName='active'>
                <i className='fas fa-star'/>
                <span className='menu-items-text'>Wishlist</span>
            </NavLink>
            <NavLink to='/settings' className='menu-items' activeClassName='active'>
                <i className='fas fa-cog'/>
                <span className='menu-items-text'>Settings</span>
            </NavLink>
            <NavLink to='/account' className='menu-items' activeClassName='active'>
                <i className='fas fa-user'/>
                <span className='menu-items-text'>Account</span>
            </NavLink>
        </nav>
    </header>
);

export default Menu;