import React, {Component} from 'react';
import FloatButton from "./FloatButton";
import SearchInput from "../SearchInput";
import Item from "./Item";
import ItemList from "./ItemList";
import Aside from "../Aside/Aside";
import {connect} from 'react-redux';
import {getItems} from "../../actions/items";

class Home extends Component {

    state = {
        search: '',
        itemsFilter: []
    };

    componentDidMount() {
        const {getItems} = this.props;
        getItems();
    }

    renderItems = items => items.map(item => (
        <ItemList key={item.id}>
            <Item item={item}/>
        </ItemList>
    ));

    filterCity = event => {
        this.setState({[event.target.name]: event.target.value.toLowerCase()});
        const {items} = this.props;
        let {search} = this.state;
        const itemsFilter = items.filter(item => item.origin.toLowerCase().includes(search) || item.destination.toLowerCase().includes(search));
        this.setState({itemsFilter});
    };

    render() {
        const {items, load} = this.props;
        const {search, itemsFilter} = this.state;
        return (
            <section className='home'>
                <div className="home-aside">
                    <Aside/>
                </div>
                <div className="home-content">
                    <ItemList>
                        <SearchInput placeholder='search by city' search={search} filterCity={this.filterCity}/>
                    </ItemList>
                    <div className='items'>
                        {
                            load ? 'loading...' : items && this.renderItems(search ? itemsFilter : items)
                        }
                    </div>
                </div>
                <FloatButton/>
            </section>
        )
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getItems: () => dispatch(getItems())
    }
};

const mapStateToProps = ({items}) => {
    return {
        items: items.items,
        load: items.load
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);