import actionTypes from "./actionTypes";
import API from "../Api";

export const loadItems = payload => ({type: actionTypes.GET_ITEMS_LOAD, payload});
export const getItems = () => {
    return (dispatch) => {
        dispatch(loadItems(true));
        API.getItems().then(payload => {
            dispatch({type: actionTypes.GET_ITEMS_SUCCESS, payload});
        }).catch(err => {
            dispatch({type: actionTypes.GET_ITEMS_ERROR, err})
        });
    }
};